import React, { Fragment, useContext, useState } from "react";
import { AddContext } from "../../store/ItemsContext";
import Modal from "../../UI/Modal";
import './Cart.css';
import CartItem from "./CartItem";
import Checkout from "./Checkout";

const Cart = (props) => {

    const ctxData = useContext(AddContext)
    const [isCheckout, setIscheckout] = useState(false);
    const totalAmount = `$${ctxData.totalAmount.toFixed(2)}`;
    const HasItems = ctxData.items.length > 0;
    const cartItemRemoveHandler = id => {
        ctxData.removeItem(id);

    }
    const cartItemAddHandler = item => {
        ctxData.addItem(item);
    }


    const cartitems = <ul className="cartitems">
        {ctxData.items.map((item) => <li>
            <CartItem
                key={item.id}
                name={item.name}
                amount={item.amount}
                price={item.price}
                onRemove={cartItemRemoveHandler.bind(null, item.id)}
                onAdd={cartItemAddHandler.bind(null, item)} />
        </li>)}</ul>



    const HideCartHandler = () => {
        ctxData.cart[1](false);
    }
    const orderHandler = () => {
        setIscheckout(true);
    }

    const modelAction = <div className='actions'>
        <button className="closebutton" onClick={HideCartHandler}>Close</button>
        {HasItems && <button className='button' onClick={orderHandler}>Order</button>}
    </div>


    return (
        <Fragment>
            {ctxData.cart[0] && <Modal>
                {cartitems}
                <div className='total'>
                    <span>Total Amount</span>
                    <span>{totalAmount}</span>
                </div>
                {isCheckout && <Checkout onCancel ={HideCartHandler}/>}
                {!isCheckout && modelAction}

            </Modal>
            }
        </Fragment>
    );

}
export default Cart;