
import { createContext, Fragment, useState, useReducer } from 'react';
export const AddContext = createContext({
    cart: [],
    items: [],
    totalAmount: 0,
    addItem: (item) => { },
    removeItem: (id) => { }
});
const defaultCartState = {
    items: [],
    totalAmount: 0
}
const cartReducer = (state, action) => {
  if (action.type === 'ADD'){

    const updatedTotalAmount = state.totalAmount + action.item.price * action.item.amount;
    const exitingCartItemIndex = state.items.findIndex(item => 
        item.id === action.item.id);
    const exitingCartItem = state.items[exitingCartItemIndex]
    let updatedItems ;
    if(exitingCartItem){
       const updatedItem = {
            ...exitingCartItem,
            amount :exitingCartItem.amount+action.item.amount

        };
        updatedItems= [...state.items];
        updatedItems[exitingCartItemIndex] = updatedItem
    }
    else{
        updatedItems = state.items.concat(action.item);

    }




    return {
        items:updatedItems,
        totalAmount:updatedTotalAmount
    };}
    if (action.type === 'REMOVE'){
        const exitingCartItemIndex = state.items.findIndex(
            item => item.id === action.id
            );
            const exitingItem = state.items[exitingCartItemIndex]
            const updatedTotalAmount = state.totalAmount-exitingItem.price;
         let updatedItems; 
         if(exitingItem.amount === 1){
         updatedItems =state.items.filter(item =>item.id !==action.id)

         }else{
          const updatedItem = {...exitingItem,amount:exitingItem.amount-1};
           updatedItems=[...state.items];
           updatedItems[exitingCartItemIndex] = updatedItem;
         }
    return {
    items:updatedItems,
    totalAmount:updatedTotalAmount
    }


    }


    return defaultCartState

}


export function ItemContext(props) {


    const [cartState, dipatchCartAction] = useReducer(cartReducer, defaultCartState)
    const [cartIsShown, setCartIsShown] = useState(false);

    const addItemHanlder = item => {
        dipatchCartAction({type :'ADD', item:item})
    };
    const removeItemHandler = id => {
        dipatchCartAction({type:'REMOVE', id:id})
    };
    const ctxData = {
        cart: [cartIsShown, setCartIsShown],
        items: cartState.items,
        totalAmount: cartState.totalAmount,
        addItem: addItemHanlder,
        removeItem: removeItemHandler
    };

    return (
        <Fragment>
            <AddContext.Provider value={ctxData}>
                {props.children}
            </AddContext.Provider>

        </Fragment>
    )
}